<template>
    <div>
        <table v-if="order.ddno!=null"  class="smart-table" border="1">
            <tr>
                <td class="tlabel">订单编号</td>
                <td>{{order.ddno}}</td>
                <td class="tlabel">下单时间:</td>
                <td>
                    {{order.createtime}}

                </td>
            </tr>



        </table>
        <table   width="100%" border="0" cellspacing="0" cellpadding="0" class="ui-record-table">


            <thead>

            <tr>
                <th>名称</th>
                <th>价格</th>
                <th>数量</th>
                <th>状态</th>
            </tr>
            </thead>
            <tbody>

            <tr >
                <td><img width="80" height="80" :src="hostHead+od.tupian"/>
                    {{od.spname}}</td>
                <td>{{od.price}}元</td>
                <td>{{od.count}}</td>
                <td>
                    <span v-if="od.state==1">待付款</span>
                    <span v-if="od.state==2">待派送</span>
                    <span v-if="od.state==3">待评价</span>
                    <span v-if="od.state==4">已收货</span>
                    <span v-if="od.state==5">已取消</span>
                    <span v-if="od.state==6">已申请售后</span>

                </td>
            </tr>


            </tbody>
        </table>

        <table v-if="order.ddno!=null"  class="smart-table" border="1">
            <tr>
                <td class="tlabel" align="right">原因:</td>
                <td>
                    <el-radio-group v-model="shouhou.reason">
                        <el-radio label="不喜欢"></el-radio>
                        <el-radio label="和商品描述不符"></el-radio>
                        <el-radio label="7天内退货"></el-radio>
                    </el-radio-group>

                </td>
            </tr>

            <tr>
                <td class="tlabel" align="right">描述:</td>
                <td>

                    <el-input v-model="shouhou.des" type="textarea" style="width:70%;" placeholder="描述"></el-input>


                </td>
            </tr>

            <tr>
                <td colspan="4">
                    <el-button type="primary" @click="thHandler">提交</el-button>
                </td>
            </tr>




        </table>


    </div>
</template>

<script>
    export default {
        name: "shouhou.vue",
        data(){
            return {

                hostHead:this.$app.contextPath,
                order: {},
                od:{},
                receaddress: null,
                shouhou:{
                    reason:"7天内退货",
                    orderno:"",
                    tdes:"",
                    spid:0,
                    spname:'',
                    count:1,
                    tupian:'',
                    price:0,
                    oid:1,
                    oiid:1
                },
            }
        },

        props:{
            params:{
                type:Object,
                default:{}
            },
        },
        methods:{


            async getOrderInfo(){

                let {data:res}= await this.$http.post("admin/shorder/load",{id:this.params.id});
                if (res.stateCode<0){
                    this.$message.error(res.des);
                    return ;
                }
                if(res.data!=null&&res.data.orderDetail!=null) {
                    res.data.orderDetail = res.data.orderDetail.filter(c => {
                        return c.id == this.params.oiid;
                    });

                }
                this.order=res.data;
                this.od= this.order.orderDetail[0];
                this.shouhou.oiid = this.od.id;
                this.shouhou.spname = this.od.spname;
                this.shouhou.spid = this.od.spid;
                this.shouhou.tupian = this.od.images[0];
                this.shouhou.count = this.od.count;
                this.shouhou.price = this.od.price;
                this.shouhou.totalfee = this.od.totalfee;
                this.shouhou.oid =this.order.id;
                this.shouhou.orderno =this.order.ddno;
            },
            async getAddress(){

                let {data:res}=await this.$http.post("admin/receaddress/load",{id:this.order.addid});

                if (res.stateCode<0){
                    this.$message.error(res.des);
                    return ;
                }
                this.receaddress=res.data;
                console.log("receaddress",this.receaddress);

            },
            async thHandler(){

                let res = this.$confirm('是否提交?', '系统提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning',
                }).then(async () => {
                    let {data:res}=await this.$http.post("admin/shouhou/save", this.shouhou);
                    if (res!=null&&res.stateCode<0){
                        this.$message.error(res.des);
                        return ;
                    }
                    this.$emit("close",false);
                }).catch(() => {
                });

            }

        },
        async created(){
            await this.getOrderInfo();
            await this.getAddress();
        }

    }
</script>

<style scoped>

</style>
